import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router);

const index = ()=> import('../view/index');

const router = new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    },
    {
      path: '/index',
      name: 'index',
      component: index
    },
  ]
})
export default router;



